import ExtensionPage from 'flarum/components/ExtensionPage';
import LoadingIndicator from 'flarum/components/LoadingIndicator';
import Button from 'flarum/components/Button';
import LinksQueueAddModal from './LinksQueueAddModal';
import LinksQueueListItem from './LinksQueueListItem';
import Sortable from 'sortablejs';
export default class SettingsPage extends ExtensionPage {
  oninit(attrs) {
    super.oninit(attrs);
    this.loading = false;
    this.linksQueueList = [];
    this.loadResults();
  }
  initSort() {
    let el = document.getElementById('linksQueueSortableItems');
    let sortable = Sortable.create(el, {
      animation: 150,
      swapThreshold: 0.65,
      onEnd: e => this.updateSort(e)
    });
  }
  content() {
    return m("div", {
      className: "ExtensionPage-settings FlarumBadgesPage"
    }, m("div", {
      className: "container"
    }, m("div", {
      style: "padding-bottom:10px"
    }, m(Button, {
      className: 'Button',
      onclick: () => app.modal.show(LinksQueueAddModal)
    }, app.translator.trans('wusong8899-links-queue.admin.link-add'))), m("ul", {
      id: "linksQueueSortableItems",
      style: "padding:0px;list-style-type: none;",
      oncreate: this.initSort.bind(this)
    }, this.linksQueueList.map(LinksQueueItemData => {
      return m("li", {
        itemID: LinksQueueItemData.id(),
        style: "margin-top:5px;background: var(--body-bg);"
      }, LinksQueueListItem.component({
        LinksQueueItemData
      }));
    }))));
  }
  updateSort(e) {
    const newIndex = e.newIndex;
    const oldIndex = e.oldIndex;
    if (newIndex !== oldIndex) {
      const children = e.from.children;
      const linkQueueOrder = {};
      for (let i = 0; i < children.length; i++) {
        const child = children[i];
        const itemID = $(child).attr("itemID");
        linkQueueOrder[itemID] = i;
      }
      app.request({
        url: "".concat(app.forum.attribute('apiUrl'), "/linksQueueList/order"),
        method: 'POST',
        body: {
          linkQueueOrder
        }
      });
    }
  }
  parseResults(results) {
    [].push.apply(this.linksQueueList, results);
    m.redraw();
    return results;
  }
  loadResults() {
    return app.store.find("linksQueueList").catch(() => {}).then(this.parseResults.bind(this));
  }
}
flarum.reg.add('wusong8899-client1-links-queue', 'admin/components/SettingsPage', SettingsPage);